.movie-card {
    margin-top: 20px;
    margin-bottom: 20px;

    .card-img {
        height: 250px;
    }

    .moviecard-body {
        height: 120px;
    }


}

.movie-card:hover {
    color: rgb(72, 5, 217);
    transform: scale(1.025);
    background: #f4ec6f;
    animation: popBackground 0.3s ease-in;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 5px 10px
}